<template>
  <ul class="bt-left container3_left">
    <li :class="{'on': isFocus === index}" v-for="(item, index) in userMatterList" :key="item" @click="focus(index)"><div class="bt-right" :class="'icon'+(index + 1)">{{item}}</div></li>
  </ul>
</template>

<script>
export default {
  name: 'UserMatterList',
  data () {
    return {
      userMatterList: ['我的办件', '我的收藏', '我的账单', '我的咨询', '我的建议', '我的投诉', '我的消息', '我的服务', '我的足迹', '我的资料库'],
      isFocus: 0
    }
  },
  methods: {
    focus: function (index) {
      this.isFocus = index
      this.$emit('focusIndex', { 'index': this.isFocus })
    }
  }
}
</script>

