<template>
  <div class="home">
    <ul>
      <li><router-link to="/">首页</router-link></li>
      <li><router-link to="/Login">登录</router-link></li>
      <li><router-link to="/Register">注册</router-link></li>
      <li><a href="javascript:;" @click="checkLogin">用户中心</a></li>
    </ul>
  </div>
</template>

<script>
// @ is an alias to /src
import main from '../main'

export default {
  name: 'home',
  mounted: function () {
    this.$nextTick(function () {
      if (localStorage.getItem('isLogin')) {
        alert('You have logined before.')
        location.href = '/#/usercenter'
      }
    })
  },
  methods: {
    checkLogin: function () {
      if (main.state.isLogin) {
        window.open('/#/UserCenter')
      } else {
        alert('You have not login, please login first!')
      }
    }
  }
}
</script>
