<template>
  <ul class="fw_maybe_list">
    <li v-for="item in serviceData" :key="item.serviceName">
      <a href="">
        <img :src="item.serviceImg" :alt="item.serviceName" />
        <div class="fw_t">
          <div class="fw_name">{{item.serviceName}}</div>
          <div class="fw_abstract">{{item.serviceAbs}}</div>
        </div>
      </a>
    </li>
  </ul>
</template>

<script>
import axios from 'axios'

export default {
  name: 'PossibleService',
  data () {
    return {
      serviceData: []
    }
  },
  mounted: function () {
    this.$nextTick(function () {
      let _this = this
      axios.get('/static/data/personalService.json').then(function (res) {
        _this.serviceData = res.data.results
      })
    })
  }
}
</script>

