<template>
  <div class="grzx_list">
    <div class="bj_title">办件列表</div>
    <ul class="bjlb_list">
      <li v-for="(item, index) in showList" :key="index">
        <div class="bt-left bj-time"><div class="bj-date">{{item.endTime.substr(item.endTime.length - 2, 2)}}</div><span class="bj-month">{{item.endTime.substr(0, item.endTime.length - 3)}}</span></div>
        <div class="bt-left bj-con"><div class="bjName"><a href="">{{item.title}}</a></div><div class="bjqk"><span class="slbm">受理部门：{{item.dept}}</span><span class="sbsj">申报时间：{{item.createTime | filterTime}}</span></div></div>
        <div class="bt-right bj-status"><a href=""><div class="pj" v-if="item.canEvaluate">我要评价</div></a><div class="ybj" v-if="item.isEnd">已办结</div></div>
      </li>
    </ul>
    <div class="btfy clearfix">
      <a class="prev_page" @click="index === 0 ? 0 : index--">上一页</a>
      <a class="fy" v-for="item in pageIndex" :key="item" @click="changePage(item)" :class="{ 'list_on': item === index + 1 }">{{item}}</a>
      <a class="next_page" @click="index === pageArray.length ? pageArray.length : index++">下一页</a>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'MyMatter',
  data () {
    return {
      matterIndex: 0,
      listData: [],
      index: 0,
      pages: 0,
      pageArray: [],
      pageIndex: []
    }
  },
  mounted: function () {
    let _this = this
    axios.get('/static/data/banjian.json').then(res => {
      _this.listData = res.data.result
      this.$nextTick(function () {
        this.pages = this.listData.length % 5 === 0 ? this.listData.length / 5 : Math.floor(this.listData.length / 5) + 1
        for (let i = 0; i < this.pages; i++) {
          this.pageArray.push(i + 1)
        }
        let len = this.pageArray.length
        if (len >= 7) {
          this.pageIndex = this.pageArray.slice(0, 6)
        } else {
          this.pageIndex = this.pageArray
        }
      })
    })
  },
  filters: {
    filterTime: function (val) {
      return val.replace('-', '年').replace('-', '月') + '日'
    }
  },
  computed: {
    showList: function () {
      return this.listData.slice(this.index * 5, (this.index + 1) * 5)
    }
  },
  methods: {
    changePage: function (index) {
      this.index = index - 1
    }
  },
  watch: {
    index: function () {
      if (this.pageArray.length !== this.pageIndex.length) {
        if (this.index - 2 < this.pageIndex[0] && this.pageIndex[0] !== 1) {
          this.pageIndex.pop()
          this.pageIndex.unshift(this.pageIndex[0] - 1)
        }
        if (this.index + 3 > this.pageIndex[5] && this.pageIndex[5] !== this.pageArray.length) {
          this.pageIndex.push(this.pageIndex[5] + 1)
          this.pageIndex.shift()
        }
      }
    }
  }
}
</script>

