<template>
  <div class="user clearfix">
    <img :src="photo" :alt="name" class="bt-left userPic" />
    <div class="bt-left userBox">
      <div class="userName">你好，{{name}}！</div>
      <div class="smrz" v-if="isVertify">已实名认证</div>
      <div class="rzdj" v-if="thisLevel">认证等级：二级</div>
    </div>
    <div class="bt-left userOther">
      <div class="userInfo">
        <div class="bt-left sex">性别：{{sex ? '女' : '男'}}</div>
        <div class="bt-left birth">出生日期：{{birth | filterBirth}}</div>
        <div class="bt-left ic">身份证号：{{ic | filterId}}</div>
        <div class="bt-clear"></div>
        <div class="main">邮箱：{{eMail}}</div>
      </div>
      <a href="" class="frkj">进入法人空间</a>
      <a href="" class="zhsz">账户设置</a>
      <a href="" class="tcdl">退出登录</a>
    </div>
  </div>
</template>

<script>

export default {
  name: 'LoginInfo',
  data: function () {
    return {
      name: '',
      photo: '',
      age: '',
      sex: '',
      birth: '',
      ic: '888888888888888888',
      eMail: '',
      isVertify: '',
      thisLevel: '',
      isPerson: ''
    }
  },
  mounted: function () {
    this.$nextTick(function () {
      if (sessionStorage.getItem('isLogin')) {
        let data = JSON.parse(sessionStorage.getItem('data'))
        this.name = data.name
        this.photo = data.photo
        this.age = data.age
        this.sex = data.sex
        this.birth = data.birth
        this.ic = data.ic
        this.eMail = data['e-mail']
        this.isVertify = data.isVertify
        this.thisLevel = data.thisLevel
        this.isPerson = data.isPerson
      } else {
        sessionStorage.clear()
        alert('You have not logined! Please login first')
        location.href = '/#/'
      }
    })
  },
  filters: {
    filterId: function (val) {
      if (val !== '') {
        return val.replace(val.substring(0, 12), val.substring(0, 6) + '******')
      }
      return ''
    },
    filterBirth: function (val) {
      return val.replace('-', '年').replace('-', '月') + '日'
    }
  },
  methods: {

  }
}
</script>

