<template>
    <div>
      <label for="name">姓名</label>
      <input id="name" v-model="name" type="text" />
      <br />
      <label for="pwd">密码</label>
      <input id="pwd" v-model="pwd" type="password" />
      <br />
      <button @click="checkForm">提交</button>
    </div>
</template>

<script>
import axios from 'axios'
import main from '../main'

export default {
  name: 'Login',
  data () {
    return {
      'name': '张超懿',
      'pwd': '82802480'
    }
  },
  methods: {
    checkForm: function () {
      let _this = this
      axios.get('/static/data/userInfo.json').then(function (response) {
        let cache = response.data.result
        let i = 0
        for (i; i < cache.length; i++) {
          let __this = cache[i]
          if (__this.name === _this.name && __this.pwd === _this.pwd) {
            sessionStorage.clear()
            sessionStorage.setItem('isLogin', 'true')
            sessionStorage.setItem('data', JSON.stringify(__this))
            main.commit('loginSuccess')
            location.href = '/#/usercenter'
            break
          }
        }
      })
    }
  },
  mounted: function () {
    this.$nextTick(function () {
      if (sessionStorage.getItem('isLogin')) {
        alert('You have logined before, don\'t login again!')
        location.href = '/#/usercenter'
      }
    })
  }
}
</script>

